.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	padding: 14px 0;
	z-index: 1000;
	@include tr(.4s);
	&__list {
		display: inline-block;
		vertical-align: top;
		padding: 0;
		margin: 0;
		list-style: none;
		li {
			display: inline-block;
			vertical-align: top;
			margin: 0 12px;
			@include r(1220) {
				text-align: center;
				display: block;
				margin-bottom: 12px; }
			&.is-active,
			&:hover {
				a {
					color: $yellow; } } }
		a {
			color: #fff;
			@include tr(.4s); } }
	&__wrap {
		@include r(1220) {
			position: fixed;
			top: 0;
			bottom: 0;
			left: 100%;
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			background-color: rgba(0, 0, 0, .9);
			z-index: 1000; } }
	.layout-s {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		position: relative; }
	.layout-x {
		@include r(650) {
			padding: 0 10px; } } }

.logo {
	position: relative;
	display: inline-block;
	vertical-align: top;
	font-size: 0;
	text-transform: uppercase;
	white-space: nowrap;
	// font-size: 56px
	// line-height: .9
	color: #fff;
	letter-spacing: -.07em;
	z-index: 1;
	// +hbl
	svg {
		display: inline-block;
		vertical-align: top;
		height: 40px;
		fill: #fff;
		@include r(400) {
			height: 32px; }
		@include r(360) {
			height: 26px; } } }


.trigger {
	position: absolute;
	top: 30px;
	right: 20px;
	display: flex;
	align-items: center;
	cursor: pointer;
	user-select: none;
	display: none;
	z-index: 1010;
	@include r(1200) {
		display: flex; }
	@include r(500) {
		right: 10px;
		top: 20px; }
	@include r(400) {
		top: 10px; }
	&__btn {
		display: inline-block;
		vertical-align: top;
		position: relative;
		width: 36px;
		height: 20px;
		padding: 2px;
		z-index: 9;
		cursor: pointer;
		text-align: right;
		&:hover {
			opacity: .7; } }

	&.is-active {
		border-color: transparent !important;
		z-index: 1010;
		&:hover {
			opacity: .9; }
		.line {
			width: 32px;
			top: 11px;
			&-1 {
				@include rotate(45); }
			&-2 {
				@include rotate(-45); } }
		+ .header__wrap {
			left: 0; } } }
.line {
	position: absolute;
	right: 0;
	display: inline-block;
	vertical-align: top;
	transform-origin: 50% 50%;
	height: 2px;
	background-color: #fff;
	@include tr(.6s);
	&-1 {
		top: 4px;
		width: 32px; }
	&-2 {
		width: 26px;
		bottom: 4px; } }

.is-scrolled {
	.header {
		background-color: rgba(38, 37, 33, .9);
		z-index: 1000; } }
